<template lang="pug">
.all 
  .row 
    .col-xl-6.grid-margin.stretch-card <!-- 圖表外框 -->
      .col-sm-12.ml-3
        .card.card2
          .card-body
            h6.card-title 購入者數
              i.fas.fa-question.ml-3(
                type="button",
                data-toggle="modal",
                data-target="#exampleModal",
                @click="Description = 'card2'"
              )
            i.fas.fa-eye(@click="$chart_scale('card2')") <!-- 放大與縮小功能 -->

            .spinner-border.spinner-border-sm.chart_loading(v-if="isLoading") 
            canvas#chartjsLine2 <!-- 圖表盒子 -->
    .col-xl-6.grid-margin.stretch-card
      .col-sm-12
        .card.card3
          .card-body
            h6.card-title 売上金額
              i.fas.fa-question.ml-3(
                type="button",
                data-toggle="modal",
                data-target="#exampleModal",
                @click="Description = 'card3'"
              )
            i.fas.fa-eye(@click="$chart_scale('card3')")
            .spinner-border.spinner-border-sm.chart_loading(v-if="isLoading")
            canvas#chartjsLine3 
  .row
    .col-xl-6.grid-margin.stretch-card
      .col-sm-12.ml-3
        .card.card5
          .card-body
            h6.card-title 客単価
              i.fas.fa-question.ml-3(
                type="button",
                data-toggle="modal",
                data-target="#exampleModal",
                @click="Description = 'card5'"
              )
            i.fas.fa-eye(@click="$chart_scale('card5')")
            .spinner-border.spinner-border-sm.chart_loading(v-if="isLoading")
            canvas#chartjsLine5
    .col-xl-6.grid-margin.stretch-card
      .col-sm-12
        .card.card6
          .card-body
            h6.card-title 新規 / 既存
              i.fas.fa-question.ml-3(
                type="button",
                data-toggle="modal",
                data-target="#exampleModal",
                @click="Description = 'card6'"
              )
            i.fas.fa-eye(@click="$chart_scale('card6')")
            .spinner-border.spinner-border-sm.chart_loading(v-if="isLoading")
            canvas#chartjsLine6
  .row
    .col-xl-6.grid-margin.stretch-card
      .col-sm-12.ml-3
        .card.card7
          .card-body
            h6.card-title 定期 / 都度
              i.fas.fa-question.ml-3(
                type="button",
                data-toggle="modal",
                data-target="#exampleModal",
                @click="Description = 'card7'"
              )
            i.fas.fa-eye(@click="$chart_scale('card7')")

            .spinner-border.spinner-border-sm.chart_loading(v-if="isLoading")
            canvas#chartjsLine7
  #exampleModal.modal.fade(
    tabindex="-1",
    role="dialog",
    aria-labelledby="exampleModalLabel",
    aria-hidden="true"
  ) <!-- ?號 的提示盒子 -->
    .modal-dialog.modal-dialog-centered(
      role="document",
      v-show="Description == item.name",
      v-for="item in Description_context"
    )
      .modal-content
        .modal-header
          h5#exampleModalLabel.modal-title {{ item.title }}
          button.close(type="button", data-dismiss="modal", aria-label="Close")
            span(aria-hidden="true") &times;
        .modal-body <!-- 盒子內容 -->
          span {{ item.context1 }} <br> 
          span {{ item.context2 }}
        .modal-footer
          button.btn.btn-secondary(type="button", data-dismiss="modal") Close

  keep-alive 
    CROSTABLE(v-if="sendNow")  <!-- 表格 -->
</template>

<script>
import CROSTABLE from "@/components/table/cros-table";
export default {
  components: {
    CROSTABLE,
  },
  data() {
    return {
      sendNow: false, //表格顯示條件
      Description: "card2", //當前提示框
      Description_context: [
        // 提示框的內容
        {
          title: "購入者數",
          name: "card2",
          context1: "Data Source：受注一覧２",
          context2: "Data Column：１受注データ＝１購入者数",
        },
        {
          title: "売上金額",
          name: "card3",
          context1: "Data Source：受注一覧２",
          context2: "Data Column：【受注金額】の総合",
        },
        {
          title: "客単価",
          name: "card5",
          context1: "Data Source：受注一覧２",
          context2: "Formula：【売上金額】 / 【購入者数】",
        },
        {
          title: "新規 / 既存",
          name: "card6",
          context1: "Data Source：受注一覧２",
          context2: "Data Column：【新規/既存（全受注）】の割合",
        },
        {
          title: "定期 / 都度",
          name: "card7",
          context1: "Data Source：受注一覧２",
          context2: "Data Column：【定期フラグ】の割合",
        },
      ],
      //圖表盒子變數
      a_barChartVis: barChartVis,
      a_barChartCV: barChartCV,
      a_barChartProfit: barChartProfit,
      a_barChartCVR: barChartCVR,
      a_barChartPrice: barChartPrice,
      a_barChartOrderType: barChartOrderType,
      a_barChartRegular: barChartRegular,
    }; 
  },
   watch: {
    dateType() {
      this.upDate(); //監控時間類別 更新updata
    },
    new_data() {
      this.upDate(); // 當資料更動的時候 更新updata
    },
  },
  computed: {
    isLoading() {
      return this.$store.state.cros.isLoading; // loading 變數獲取
    },
    new_data() {
      return this.$store.state.cros.data; //  獲取當前 cros data
    },
    dateType() {
      return this.$store.state.cros.dateType; // 獲取當前date 類別
    },
  },

  methods: {
    upDate() {  /* 圖表的update */
      const vm = this;
      let cros_df = this.$store.state.cros.data; // cros的資料來源
      let clients = cros_df[0].clients;
      // 判斷當前時間類別
      if (!this.$store.state.cros.dateType) {
        cros_df = cros_df[1];
      }
      if (this.$store.state.cros.dateType == "m") {
        cros_df = cros_df[2];
      }
      if (this.$store.state.cros.dateType == "q") {
        cros_df = cros_df[3];
      }
      
      vm.sendNow = true;
      let timeBox = [];
      // 圖表顏色
      let color_all = [
        "#f38181",
        "#fce38a",
        "#eaffd0",
        "#a8e6cf",
        "#fdffab",
        "#ffd3b6",
        "#ffaaa5",
        "#d4a5a5",
        "#ffecda",
        "#f9ffea",
        "#a6d0e4",
        "#ff6f3c",
        "#ff9a3c",
        "#ffc93c",
        "#17b978",
        "#a7ff83",
      ];
      let cros_charts = [[], [], []]; //預設產生前三個圖表
      let OrderType = [ // 預設產生圖表
        //新規率 既存率 -
        {
          data: [],
          label: "新規",
          backgroundColor: "#f38181",
        },
        {
          data: [],
          label: "既存",
          backgroundColor: "#fce38a",
        },
      ]; //定期率+都度率
      let Regular = [ // 預設產生圖表
        {
          data: [],
          label: "定期",
          backgroundColor: "#f38181",
        },
        {
          data: [],
          label: "都度",
          backgroundColor: "#fce38a",
        },
      ]; //新規率+既存率
         
      // 依據當前客戶數產生陣列與預設資料
      for (let cli in clients) {
        for (let charts in cros_charts) {
          cros_charts[charts].push({
            data: [],
            label: clients[cli],
            borderColor: color_all[cli] || "#7ee5e5",
            backgroundColor: "#fff",
            fill: false,
          }); //
        } 
        OrderType[0].data.push({
          x: clients[cli],
          y: 0,
        });
        OrderType[1].data.push({
          x: clients[cli],
          y: 0,
        });
        Regular[0].data.push({
          x: clients[cli],
          y: 0,
        });
        Regular[1].data.push({
          x: clients[cli],
          y: 0,
        });
        let [cvBox, profitBox, priceBox] = cros_charts;

        cros_df.forEach((item) => {
          if (item.clientname.includes(clients[cli]) == 1)
            timeBox.push(item.date);
          if (item.clientname == cvBox[cli].label)
            cvBox[cli].data.push({
              x: item.date,
              y: item.cv,
            });
          if (item.clientname == profitBox[cli].label)
            profitBox[cli].data.push({
              x: item.date,
              y: item.profit,
            });
          if (item.clientname == priceBox[cli].label)
            priceBox[cli].data.push({
              x: item.date,
              y: item.unitprice,
            });
          if (item.clientname == OrderType[0].data[cli].x)
            OrderType[0].data[cli].y += item.ordertype[0];

          if (item.clientname == OrderType[1].data[cli].x)
            OrderType[1].data[cli].y += item.ordertype[1];
          if (item.clientname == Regular[0].data[cli].x) {
            Regular[0].data[cli].y += item.regular[0];
          }
          if (item.clientname == Regular[1].data[cli].x) {
            Regular[1].data[cli].y += item.regular[1];
          }
        }); 
        
        // 時間排序
        timeBox = timeBox.filter((item, key, arr) => arr.indexOf(item) == key);
        timeBox.sort((a, b) => (a > b ? 1 : -1)); //
         // 給入圖表
        vm.a_barChartCV.datasets = cvBox;
        vm.a_barChartProfit.datasets = profitBox;
        vm.a_barChartPrice.datasets = priceBox;
        vm.a_barChartOrderType.datasets = OrderType;
        vm.a_barChartRegular.datasets = Regular;
        vm.a_barChartCV.labels = timeBox;
        vm.a_barChartProfit.labels = timeBox;
        vm.a_barChartPrice.labels = timeBox;
        vm.a_barChartOrderType.labels = clients;
        vm.a_barChartRegular.labels = clients;
      } 
      
      //更新
      window.myBar2.update();
      window.myBar3.update();
      window.myBar5.update();
      window.myBar6.update();
      window.myBar7.update();
    }, 
  },

  activated: function () {
    // 圖表預設函數
    $(function () {
      if ($("#chartjsLine").length) {
        (Chart.defaults.global.defaultFontFamily = "Noto Sans JP"),
          (window.myBar1 = new Chart($("#chartjsLine"), {
            type: "line",
            data: barChartVis,
          }));
      }

      if ($("#chartjsLine2").length) {
        window.myBar2 = new Chart($("#chartjsLine2"), {
          type: "line",
          data: barChartCV,
          options: {
            scales: {
              xAxes: {
                type: "time",
                distribution: "linear",
              },
            },
          },
        });
      }

      if ($("#chartjsLine3").length) {
        window.myBar3 = new Chart($("#chartjsLine3"), {
          type: "line",
          data: barChartProfit,
        });
      }

      if ($("#chartjsLine4").length) {
        window.myBar4 = new Chart($("#chartjsLine4"), {
          type: "line",
          data: barChartCVR,
        });
      }

      if ($("#chartjsLine5").length) {
        window.myBar5 = new Chart($("#chartjsLine5"), {
          type: "line",
          data: barChartPrice,
        });
      }
      if ($("#chartjsLine6").length) {
        window.myBar6 = new Chart($("#chartjsLine6"), {
          type: "bar",
          data: barChartOrderType,
          options: {
            title: {
              display: false,
            },
            tooltips: {
              mode: "index",
              intersect: false,
            },
            responsive: true,
            scales: {
              xAxes: [
                {
                  stacked: true,
                },
              ],
              yAxes: [
                {
                  stacked: true,
                },
              ],
            },
          },
        });
      }
      if ($("#chartjsLine7").length) {
        window.myBar7 = new Chart($("#chartjsLine7"), {
          type: "bar",
          data: barChartRegular,
          options: {
            title: {
              display: false,
            },
            tooltips: {
              mode: "index",
              intersect: false,
            },
            responsive: true,
            scales: {
              xAxes: [
                {
                  stacked: true,
                },
              ],
              yAxes: [
                {
                  stacked: true,
                },
              ],
            },
          },
        });
      }
    }); //function end

   // this.$store.dispatch("cros/getDownload"); //獲取下載資料
  },
   
};
// 圖表盒子 
var barChartVis = {
  labels: [],
  datasets: [],
};

var barChartCV = {
  labels: [],
  datasets: [],
};

var barChartProfit = {
  labels: [],
  datasets: [],
};

var barChartCVR = {
  labels: [],
  datasets: [],
};

var barChartPrice = {
  labels: [],
  datasets: [],
};
var barChartOrderType = {
  labels: [],
  datasets: [],
};
var barChartRegular = {
  labels: [],
  datasets: [],
};
var barChartOrderType = {
  labels: [],
  datasets: [],
};
var barChartRegular = {
  labels: [],
  datasets: [],
};
</script>

<style lang="scss" scoped>
</style>


