<template lang="pug">
#app
  .main-wrapper <!-- 全域盒子 -->
    router-view(name="sidebar") <!-- 側邊bar -->
    TOPBAR(v-show="$route.name!= 'login'") <!-- 上邊bar -->
    div 
      router-view(name="filter", v-show="closeFilter") <!-- 篩選框 -->
    .page-wrapper <!-- 頁面內容 -->
      .page-content(v-show="$route.name!= 'login'")
        .d-flex.justify-content-between.align-items-center.flex-wrap.grid-margin
          ul.dateChange(v-show="$route.name!= 'home'") <!-- 時間類別選擇 -->
            label(
              @click="typeChang('q')",
              :class="{ 'date-active': dateType == 'q' }"
            ) 季
            label(
              @click="typeChang('m')",
              :class="{ 'date-active': dateType == 'm' }"
            ) 月
            label(
              @click="typeChang('')",
              :class="{ 'date-active': dateType == '' }"
            ) 日
          .datepicker-group(v-show="$route.name!= 'home'") <!-- 時間區段選擇 -->
            input.datepicker_box(
              type="date",
              v-model="date2",
              @change="dateChange"
            )
            input.datepicker_box(
              type="date",
              v-model="date1",
              @change="dateChange"
            )
            button.send-date(@click="sendDate") <!-- 送出檢索 -->
              | 檢索
              i.fas.fa-search.ml-1 
            .time-now.mt-2 <!-- 時間顯示 -->
              span.time-title 時間範囲
              i.fas.fa-long-arrow-alt-right.ml-2
              span.ml-2 {{ date2 }}
              span.ml-2 ～
              span.ml-2 {{ date1 }}
            li.mt-3 <!-- マージン 選擇 -->
              span.input-range マージン(%)
              input.ml-2.input-range(
                type="range",
                name="points",
                min="0",
                max="99",
                value="-1",
                v-model="range",
                step="5",
                @change="dateChange"
              ) 
              span.ml-2 {{ rangeif }}

      transition(name="page", mode="out-in") <!-- 各圖表頁面 -->
        keep-alive 
          router-view
</template>

<script>
import TOPBAR from "@/components/menu/topbar";
export default {
  name: "App",
  components: {
    TOPBAR,
  },
  data: function () {
    return {
      dateType: "", //日期顯示類別
      date1: "", //時間用
      date2: "", //時間用
      range: 0, //  マージン
    };
  },
  created() {
    let theDay = new Date(); // 建立時間物件
    let changeDay = 30; // 設定要往前或往後幾天
    this.date1 = theDay.toISOString().substring(0, 10); //初始時間擷取前10個字元
    let timeStamp = theDay.setDate(theDay.getDate() - changeDay); // theDay.getDate() 是用來取得今天是幾號
    this.date2 = theDay.toISOString().substring(0, 10); //初始時間擷取前10個字元
    let date1 = this.date1;
    let date2 = this.date2;
    let margin = this.rangeif;
    this.$store.dispatch("cros/getTime", { date1, date2, margin }); //  get當前時間參數
    this.$store.dispatch("fb/getTime", { date1, date2, margin }); //  get當前時間參數
    this.$store.dispatch("line/getTime", { date1, date2, margin }); //  get當前時間參數
  },
  computed: {
    closeFilter() {
      return this.$store.state.filter; // 篩選框判斷
    },
    rangeif() {
      if (this.range == 95) {
        return 99;
      } else {
        return this.range;
      }
    },
  },
  methods: {
    typeChang(type) {
      this.dateType = type;
      this.$store.dispatch("cros/timeType", type); //時間類別更改
      this.$store.dispatch("fb/timeType", type);
      this.$store.dispatch("line/timeType", type);
    },
    dateChange() {
      let date1 = this.date1;
      let date2 = this.date2;
      let margin = this.rangeif;
      this.$store.dispatch("cros/getTime", { date1, date2, margin }); //時間區段更改
      this.$store.dispatch("fb/getTime", { date1, date2, margin });
      this.$store.dispatch("line/getTime", { date1, date2, margin });
    },
    sendDate() {
      if (this.$route.name.includes("cros")) {
        //cros
        this.$store.dispatch("cros/getData"); // cros 送出api
        //this.$store.dispatch('cros/getDownload'); // cros 取的下載api
      }
      if (this.$route.name.includes("fb")) {
        this.$store.dispatch("fb/getData"); // fb  送出aip

        // this.$store.dispatch('fb/getDownload'); // fb 取得下載api
      }
      if (this.$route.name.includes("line")) {
        this.$store.dispatch("line/getData"); // line 送出api
        //this.$store.dispatch('line/getDownload'); // line 取得下載api
      }
    },
  },
};
</script>

<style lang="scss">
@import "@/assets/all.scss"; //所有css接口
</style>



