import axios from 'axios'  //主要的ajax套件


export default {
    namespaced: true,
    state:{
        head: "ind", // 指標名稱
        isLoading: false , //fb頁面的loading
        isLoading_filter: false,
        data:[],   // fb的資料包
        margin:0,
        cli : [],  // 客戶
        pro : [], // 產品
        account: [], // 帳戶名稱
        group : [], // 群組類別
        cname : [], // 行銷活動名稱 
        adset : [], // 廣告組合名稱
        adn : [], // 廣告名稱
        link: [], // link別
        ga1: [], // ga1
        ga2: [], // ga2
        ga3: [], // ga3
        date1 : "", // 篩選參數 時間別
        date2 : "",  // 篩選參數 時間別
        dateType: '', // 判定年月日
        send: 0, //送出
        download_api: "", //下載的參數
    },
    actions:{
        getHead(context,nowhead){
            context.commit('sendHead',nowhead);   
        },
        timeType(context,type){  //時間型態
            context.commit('sendDateType',type);
        },
        getTime(context,{date1,date2,margin}){  //改變時間
            context.commit('sendDate',{date1,date2,margin});
        },
        getFilter(context,{cli,pro,account,group,cname,adset,adn,link,ga1,ga2,ga3}){  //篩選改變   
            context.commit('sendFilter',{cli,pro,account,group,cname,adset,adn,link,ga1,ga2,ga3});  
        }, 
        getDownload(context){  //獲取下載api 
            let date1 = context.state.date1;
            let date2 = context.state.date2; 
            let cli = context.state.cli;
            let pro = context.state.pro;
            let account = context.state.account;
            let group = context.state.group;
            let cname = context.state.cname;
            let adset = context.state.adset;
            let adn = context.state.adn;
            let link = context.state.link;
            let ga1 = context.state.ga1;
            let ga2 = context.state.ga2;
            let ga3 = context.state.ga3;
            context.commit('sendDownApi',{date1,date2,cli,pro,account,group,cname,adset,adn,link,ga1,ga2,ga3});
        }, 
        getData(context){   // fb 的api 
            context.commit('isLoading',true);
            let date1 = context.state.date1;
            let date2 = context.state.date2; 
            let cli = context.state.cli;
            let pro = context.state.pro;
            let account = context.state.account;
            let group = context.state.group;
            let cname = context.state.cname;
            let adset = context.state.adset;
            let adn = context.state.adn;
            let link = context.state.link;
            let ga1 = context.state.ga1;
            let ga2 = context.state.ga2;
            let ga3 = context.state.ga3;
            let api = `${process.env.APIPATH}/${process.env.FACEBOOK}`;
            $("*").css("cursor",`url('/static/images/wait.ico') ,auto`);
            axios.get(api).then((res) => {
                if(res){ 
                    let fb = res.data 
                    context.commit('sendData',fb);
                    context.commit('isLoading',false);
                    $("*").css("cursor",`url('/static/images/move.ico') ,auto`);
                }else{
                    return 
                }

           })
        },// fb end

    }, //act end 
    mutations:{
        sendHead(state,nowhead){
            state.head = nowhead; 
        },
        sendData(state,data){
            state.data = data; 
        },
        isLoading(state,status){
            state.isLoading = status;
        },
        sendFilter(state,{cli,pro,account,group,cname,adset,adn,link,ga1,ga2,ga3}){
            state.cli = cli;
            state.pro = pro;
            state.account = account;
            state.group = group;
            state.cname = cname;
            state.adset = adset;
            state.adn = adn;
            state.link = link;
            state.ga1 = ga1;
            state.ga2 = ga2;
            state.ga3 = ga3;  
       },
       sendDate(state,{date1,date2,margin}){
        state.date1 = date1;
        state.date2 = date2;
        state.margin = margin;
       },
       sendDateType(state,type){
        state.dateType = type;
         },
        sendDownApi(state,{date1,date2,cli,pro,account,group,cname,adset,adn,link,ga1,ga2,ga3}){
            state.download_api = `${process.env.APIPATH}/${process.env.FACEBOOK_DOWNLOAD}`;
        }, 
    }


    
}


