<template lang="pug">
.dropdown-filter.col-sm-12
  h4.wrapper_back.mt-3(@click="backpage") 
    i.fas.fa-arrow-left.close-filter
    span.ml-3.close-filter 閉じる
  .title-filter.mb-5
    h1 検索条件 CROS
  .container 
    .wrpapperbox-set
      .wrapperbox
        .all-label-box
          .row(v-show="nowstep == 'one'")
            .labelbox.col-4
              .col-12
                h4 産業別
                  i.fas.fa-eraser(@click="clickClear('ind')")
                input.search_box(placeholder="キーワード入力", v-model="ind_filter") 

                .label-wrapper
                  label 
                    input(
                      type="checkbox",
                      @change="clickAll('ind')",
                      v-model="allSelected_ind"
                    )
                    | 全選
                  label(v-for="item in indFD") 
                    input(
                      type="checkbox",
                      @change="clickFilter('ind')",
                      v-model="ind_send",
                      :value="item"
                    )
                    | {{ item }}
            .labelbox.col-4
              .col-12
                h4 クライアント
                  i.fas.fa-eraser(@click="clickClear('cli')")
                input.search_box(placeholder="キーワード入力", v-model="cli_filter") 
                .label-wrapper 
                  label 
                    input(
                      type="checkbox",
                      @change="clickAll('cli')",
                      v-model="allSelected_cli"
                    )
                    | 全選
                  label(v-for="item in cliFD") 
                    input(
                      type="checkbox",
                      @change="clickFilter('cli')",
                      v-model="cli_send",
                      :value="item"
                    )
                    | {{ item }}
            .labelbox.col-4
              .col-12
                h4 商品名
                  i.fas.fa-eraser(@click="clickClear('pro')")
                input.search_box(placeholder="キーワード入力", v-model="pro_filter") 
                .label-wrapper 
                  label 
                    input(
                      type="checkbox",
                      @change="clickAll('pro')",
                      v-model="allSelected_pro"
                    )
                    | 全選
                  label(v-for="item in proFD") 
                    input(
                      type="checkbox",
                      @change="clickFilter('pro')",
                      v-model="pro_send",
                      :value="item"
                    )
                    | {{ item }}
          .row(v-show="nowstep == 'two'")
            .labelbox
              h4 支払い
                i.fas.fa-eraser(@click="clickClear('paymethod')")
              input.search_box(
                placeholder="キーワード入力",
                v-model="paymethod_filter"
              ) 
              .label-wrapper 
                label 
                  input(
                    type="checkbox",
                    @change="clickAll('paymethod')",
                    v-model="allSelected_paymethod"
                  )
                  | 全選
                label(v-for="item in paymethodFD") 
                  input(
                    type="checkbox",
                    @change="changeFilter",
                    v-model="paymethod_send",
                    :value="item.value"
                  )
                  | {{ item.name }}
            .labelbox
              h4 新規 / 既存
                i.fas.fa-eraser(@click="clickClear('ordertype')")
              input.search_box(
                placeholder="キーワード入力",
                v-model="ordertype_filter"
              ) 

              .label-wrapper 
                label 
                  input(
                    type="checkbox",
                    @change="clickAll('ordertype')",
                    v-model="allSelected_ordertype"
                  )
                  | 全選
                label(v-for="item in ordertypeFD") 
                  input(
                    type="checkbox",
                    @change="changeFilter",
                    v-model="ordertype_send",
                    :value="item"
                  )
                  | {{ item }}
            .labelbox
              h4 定期 / 都度
                i.fas.fa-eraser(@click="clickClear('regular')")
              input.search_box(placeholder="キーワード入力", v-model="regular_filter") 

              .label-wrapper 
                label 
                  input(
                    type="checkbox",
                    @change="clickAll('regular')",
                    v-model="allSelected_regular"
                  )
                  | 全選
                label(v-for="item in regularFD") 
                  input(type="checkbox", v-model="regular_send", :value="item" , @change="changeFilter")
                  | {{ item }}
          .row(v-show="nowstep == 'two'") 
            .labelbox
              h4 受注ステータス
                i.fas.fa-eraser(@click="clickClear('orderstatus')") 
              input.search_box(
                placeholder="キーワード入力",
                v-model="orderstatus_filter"
              ) 

              .label-wrapper 
                label 
                  input(
                    type="checkbox",
                    @change="clickAll('orderstatus')",
                    v-model="allSelected_orderstatus"
                  )
                  | 全選
                label(v-for="item in orderstatusFD") 
                  input(
                    type="checkbox",
                    v-model="orderstatus_send",
                    @change="changeFilter",
                    :value="item.value"
                  )
                  | {{ item.name }}
            .labelbox
              h4 受注経路
                i.fas.fa-eraser(@click="clickClear('orderRoute')") 
              input.search_box(
                placeholder="キーワード入力",
                v-model="orderRoute_filter"
              ) 

              .label-wrapper 
                label 
                  input(
                    type="checkbox",
                    @change="clickAll('orderRoute')",
                    v-model="allSelected_orderRoute"
                  )
                  | 全選
                label(v-for="item in orderRouteFD") 
                  input(
                    type="checkbox",
                    v-model="orderRoute_send",
                    @change="changeFilter",
                    :value="item"
                  )
                  | {{ item }}
            .labelbox
              h4 キャンペーン名
                i.fas.fa-eraser(@click="clickClear('campaign')") 
              input.search_box(
                placeholder="キーワード入力",
                v-model="campaign_filter"
              ) 
              .spinner-border.spinner-border-sm.chart_loading(v-if="isLoading")
              .label-wrapper 
                label 
                  input(
                    type="checkbox",
                    @change="clickAll('campaign')",
                    v-model="allSelected_campaign"
                  )
                  | 全選
                label(v-for="item in campaignFD") 
                  input(
                    type="checkbox",
                    v-model="campaign_send",
                    @change="changeFilter",
                    :value="item"
                  )
                  | {{ item }}
          .row(v-show="nowstep == 'two'") 
            .labelbox
              h4 媒体別
                i.fas.fa-eraser(@click="clickClear('media')")
              input.search_box(placeholder="キーワード入力", v-model="media_filter") 
              .spinner-border.spinner-border-sm.chart_loading(v-if="isLoading")
              .label-wrapper 
                label 
                  input(
                    type="checkbox",
                    @change="clickAll('media')",
                    v-model="allSelected_media"
                  )
                  | 全選
                label(v-for="item in mediaFD") 
                  input(type="checkbox", v-model="media_send", :value="item" ,@change="changeFilter")
                  | {{ item }}
            .labelbox
              h4 リンク別
                i.fas.fa-eraser(@click="clickClear('link_type')")
              input.search_box(
                placeholder="キーワード入力",
                v-model="link_type_filter"
              ) 
              .spinner-border.spinner-border-sm.chart_loading(v-if="isLoading")
              .label-wrapper 
                label 
                  input(
                    type="checkbox",
                    @change="clickAll('link_type')",
                    v-model="allSelected_link_type"
                  )
                  | 全選
                label(v-for="item in link_type_FD") 
                  input(
                    type="checkbox",
                    v-model="link_type_send",
                    @change="changeFilter",
                    :value="item"
                  )
                  | {{ item }}
            .labelbox
              h4 いたずら注文を抜く
                i.fas.fa-eraser(@click="clickClear('link_type')")
              input.search_box(
                placeholder="キーワード入力",
                v-model="link_type_filter"
              ) 

              .label-wrapper 
                label 
                  input(type="checkbox", v-model="del_flg_send", value="0" , @change="changeFilter")
                  |
                  | 抜く
          .row(v-show="nowstep == 'ga'") 
            .labelbox
              h4 GA1
                i.fas.fa-eraser(@click="clickClear('ga1')") 
              input.search_box(placeholder="キーワード入力") 
              .spinner-border.spinner-border-sm.chart_loading(v-if="isLoading")
              .label-wrapper 
                label 
                  input(
                    type="checkbox",
                    @change="clickAll('ga1')",
                    v-model="allSelected_ga1"
                  )
                  | 全選
                label(v-for="item in ga1FD") 
                  input(type="checkbox", v-model="ga1_send", :value="item" , @change="changeFilter")
                  | {{ item }}
            .labelbox
              h4 GA2
                i.fas.fa-eraser(@click="clickClear('ga2')")
              input.search_box(placeholder="キーワード入力", v-model="media_filter") 
              .spinner-border.spinner-border-sm.chart_loading(v-if="isLoading")
              .label-wrapper 
                label 
                  input(
                    type="checkbox",
                    @change="clickAll('ga2')",
                    v-model="allSelected_ga2"
                  )
                  | 全選
                label(v-for="item in ga2FD") 
                  input(type="checkbox", v-model="ga2_send", :value="item" , @change="changeFilter")
                  | {{ item }}
            .labelbox
              h4 GA3
                i.fas.fa-eraser(@click="clickClear('ga3')")
              input.search_box(placeholder="キーワード入力", v-model="pro_filter") 
              .spinner-border.spinner-border-sm.chart_loading(v-if="isLoading")
              .label-wrapper 
                label 
                  input(
                    type="checkbox",
                    @change="clickAll('ga3')",
                    v-model="allSelected_ga3"
                  )
                  | 全選
                label(v-for="item in ga3FD") 
                  input(type="checkbox", v-model="ga3_send", :value="item" , @change="changeFilter")
                  | {{ item }}
      .button-item
        button.send-filter(
          v-show="nowstep == 'one'",
          @click="sendFilter('more')"
        ) さらに検索
        button.send-filter(v-show="nowstep =='two'", @click="nowstep = 'one'") 戻り
        button.send-filter(v-show="nowstep =='ga'", @click="nowstep = 'two'") 戻り
        button.send-filter.send-filter-send.mb-3(@click="sendFilter") 檢索
          i.fas.fa-search.ml-1
        button.ga-filter-style(
          v-show="nowstep =='two'",
          @click="sendFilter('more')"
        )
          span GA
          i.fas.fa-chart-pie.ml-2
</template>

<script>
export default {
  data() {
    return {
      //傳參
      ind_send: [],
      cli_send: [],
      pro_send: [],
      campaign_send: [],
      media_send: [],
      paymethod_send: [],
      ordertype_send: [],
      regular_send: [],
      link_type_send: [],
      orderstatus_send: [],
      orderRoute_send: [],
      del_flg_send: [],
      ga1_send: [],
      ga2_send: [],
      ga3_send: [],
      data: [],
      ind: [],
      cli: [],
      pro: [],
      campaign: [],
      media: [],
      paymethod: [
        //支払い
        {
          name: "クレジットカード",
          value: "1",
        },
        {
          name: "代引",
          value: "2",
        },
        {
          name: "コンビニ",
          value: "5",
        },
        {
          name: "アフティ",
          value: "8",
        },
        {
          name: "LINE PAY",
          value: "9",
        },
        {
          name: "その他",
          value: "其它",
        },
      ],
      orderstatus: [
        //受注ステータス
        {
          name: "100:受注",
          value: "100",
        },
        {
          name: "200:確認待ち",
          value: "200",
        },
        {
          name: "300:配送依頼待ち",
          value: "300",
        },
        {
          name: "400:配送依頼済み",
          value: "400",
        },
        {
          name: "700:決済処理中",
          value: "700",
        },
        {
          name: "900:配送完了",
          value: "900",
        },
        {
          name: "910:キャンセル",
          value: "910",
        },
        {
          name: "920:返品（未返金）",
          value: "920",
        },
        {
          name: "930:返品（返金済）",
          value: "930",
        },
        {
          name: "940:配送不可（未返金）",
          value: "940",
        },
        {
          name: "950:配送不可（返金済）",
          value: "950",
        },
      ],
      orderRoute: ["EC", "CC", "LP", "SYSTEM", "Chat API", "APP"],
      ordertype: ["新規", "既存"], //新規既存
      regular: ["定期", "都度"], // 定都
      media: [], //媒體別
      link_type: [], //リンク別
      ga1: [],
      ga2: [],
      ga3: [],
      ind_df: [],
      cli_df: [],
      pro_df: [],
      ind_filter: "",
      cli_filter: "",
      pro_filter: "",
      campaign_filter: "",
      media_filter: "",
      link_type_filter: "",
      paymethod_filter: "",
      ordertype_filter: "",
      regular_filter: "",
      orderstatus_filter: "",
      orderRoute_filter: "",
      ga1_filter: "",
      ga2_filter: "",
      ga3_filter: "",
      allSelected_ind: false,
      allSelected_cli: false,
      allSelected_pro: false,
      allSelected_campaign: false,
      allSelected_paymethod: false,
      allSelected_ordertype: false,
      allSelected_regular: false,
      allSelected_media: false,
      allSelected_link_type: false,
      allSelected_orderstatus: false,
      allSelected_orderRoute: false,
      allSelected_ga1: false,
      allSelected_ga2: false,
      allSelected_ga3: false,
      nowstep: "one",
    };
  },

  computed: {
    isLoading() {
      return this.$store.state.cros.isLoading;
    },
    indFD() {
      const vm = this;
      let ind = [];
      ind = vm.ind.filter(function (item) {
        return item.toUpperCase().match(vm.ind_filter.toUpperCase());
      });
      return ind.filter((item, key, arr) => arr.indexOf(item) == key);
    }, // ind
    cliFD() {
      const vm = this;
      let cli = [];
      cli = vm.cli.filter(function (item) {
        return item.toUpperCase().match(vm.cli_filter.toUpperCase());
      });
      return cli.filter((item, key, arr) => arr.indexOf(item) == key);
    }, // cli
    proFD() {
      const vm = this;
      let pro = [];
      pro = vm.pro.filter(function (item) {
        return item.toUpperCase().match(vm.pro_filter.toUpperCase());
      });
      return pro.filter((item, key, arr) => arr.indexOf(item) == key);
    }, // pro
    campaignFD() {
      const vm = this;
      return vm.campaign.filter(function (item) {
        return item.toUpperCase().match(vm.campaign_filter.toUpperCase());
      });
    },
    mediaFD() {
      const vm = this;
      return vm.media.filter(function (item) {
        return item.toUpperCase().match(vm.media_filter.toUpperCase());
      });
    },
    link_type_FD() {
      const vm = this;
      return vm.link_type.filter(function (item) {
        return item.toUpperCase().match(vm.link_type_filter.toUpperCase());
      });
    },
    paymethodFD() {
      const vm = this;
      return vm.paymethod.filter(function (item) {
        return item.name.toUpperCase().match(vm.paymethod_filter.toUpperCase());
      });
    },
    ordertypeFD() {
      const vm = this;
      return vm.ordertype.filter(function (item) {
        return item.toUpperCase().match(vm.ordertype_filter.toUpperCase());
      });
    },
    regularFD() {
      const vm = this;
      return vm.regular.filter(function (item) {
        return item.toUpperCase().match(vm.regular_filter.toUpperCase());
      });
    },
    orderstatusFD() {
      const vm = this;
      return vm.orderstatus.filter(function (item) {
        return item.name
          .toUpperCase()
          .match(vm.orderstatus_filter.toUpperCase());
      });
    },
    orderRouteFD() {
      const vm = this;
      return vm.orderRoute.filter(function (item) {
        return item.toUpperCase().match(vm.orderRoute_filter.toUpperCase());
      });
    },
    ga1FD() {
      const vm = this;
      return vm.ga1.filter(function (item) {
        return item.toUpperCase().match(vm.ga1_filter.toUpperCase());
      });
    },
    ga2FD() {
      const vm = this;
      return vm.ga2.filter(function (item) {
        return item.toUpperCase().match(vm.ga2_filter.toUpperCase());
      });
    },
    ga3FD() {
      const vm = this;
      return vm.ga3.filter(function (item) {
        return item.toUpperCase().match(vm.ga3_filter.toUpperCase());
      });
    },
  },
  methods: {
    backpage() {
      this.$store.dispatch("actFilter", false);
    },
    changeFilter() {
      let cli = this.cli_send;
      let pro = this.pro_send;
      let pay = this.paymethod_send;
      let ord = this.ordertype_send;
      let reg = this.regular_send;
      let cam = this.campaign_send;
      let media = this.media_send;
      let link = this.link_type_send;
      let ord_s = this.orderstatus_send;
      let ord_r = this.orderRoute_send;
      let del_f = this.del_flg_send;
      let ga1 = this.ga1_send;
      let ga2 = this.ga2_send;
      let ga3 = this.ga3_send;
      this.$store.dispatch("cros/getFilter", {
        cli,
        pro,
        pay,
        ord,
        reg,
        cam,
        media,
        link,
        ord_s,
        ord_r,
        del_f,
        ga1,
        ga2,
        ga3,
      });

    },
    sendFilter(page) {
      // 送出API參數
      const vm = this;
      let cli = this.cli_send;
      let pro = this.pro_send;
      let pay = this.paymethod_send;
      let ord = this.ordertype_send;
      let reg = this.regular_send;
      let cam = this.campaign_send;
      let media = this.media_send;
      let link = this.link_type_send;
      let ord_s = this.orderstatus_send;
      let ord_r = this.orderRoute_send;
      let del_f = this.del_flg_send;
      let ga1 = this.ga1_send;
      let ga2 = this.ga2_send;
      let ga3 = this.ga3_send;
      let date1 = this.$store.state.cros.date1;
      let date2 = this.$store.state.cros.date2;
      let margin = this.$store.state.cros.margin;

      this.$store.dispatch("cros/getFilter", {
        cli,
        pro,
        pay,
        ord,
        reg,
        cam,
        media,
        link,
        ord_s,
        ord_r,
        del_f,
        ga1,
        ga2,
        ga3,
      });
      
      this.$store.dispatch("cros/getData");
      // this.$store.dispatch('cros/getDownload');

      if (page == "more") {
        if (this.nowstep == "one") {
          this.nowstep = "two";
        } else if (this.nowstep == "two") {
          this.nowstep = "ga";
        }
      } else {
        this.backpage();
      }

      this.getFilter(
        margin,
        cli,
        pro,
        pay,
        ord,
        reg,
        cam,
        media,
        link,
        ord_s,
        ord_r,
        del_f,
        ga1,
        ga2,
        ga3,
        date1,
        date2
      );
    },

    getFilter(
      margin,
      cli,
      pro,
      pay,
      ord,
      reg,
      cam,
      media,
      link,
      ord_s,
      ord_r,
      del_f,
      ga1,
      ga2,
      ga3,
      date1,
      date2
    ) {
      const vm = this;
      let api = `${process.env.APIPATH}/${process.env.DORENTO}`;
      this.$http.get(api).then((res) => {
        if (res) {
          if (res.data.length < 3) {
            alert("此區段沒有資料😢");
            if (this.nowstep == "two") {
              //this.nowstep = "one";
              return;
            } else if (this.nowstep == "ga") {
              //this.nowstep = "two";
              return;
            }
          }

          vm.media = res.data[0].media;
          vm.campaign = res.data[0].campaign;
          vm.link_type = res.data[0].link_type;
          vm.ga1 = res.data[0].ga1;
          vm.ga2 = res.data[0].ga2;
          vm.ga3 = res.data[0].ga3;
        } else {
          return;
        }
      });
    },
    clickFilter(value) {
      const vm = this;

      switch (value) {
        case "ind":
          vm.cli = [];
          vm.pro = [];
          for (let item in vm.data) {
            if (vm.ind_send == 0) {
              vm.cli = vm.cli_df;
              vm.pro = vm.pro_df;
            } else if (vm.ind_send.includes(vm.data[item].industry_type) == 1) {
              vm.cli.push(vm.data[item].client_name);
              vm.pro.push(vm.data[item].product_name);
            }
          }
          this.allSelected_ind = false;
          break;

        case "cli":
          vm.ind = [];
          vm.pro = [];
          for (let item in vm.data) {
            if (vm.cli_send == 0) {
              vm.ind = vm.ind_df;
              vm.pro = vm.pro_df;
            } else if (vm.cli_send.includes(vm.data[item].client_name) == 1) {
              vm.ind.push(vm.data[item].industry_type);
              vm.pro.push(vm.data[item].product_name);
            }
          }
          this.allSelected_cli = false;
          this.changeFilter();
          break;
        case "pro":
          vm.ind = [];
          vm.cli = [];
          for (let item in vm.data) {
            if (vm.pro_send == 0) {
              vm.ind = vm.ind_df;
              vm.cli = vm.cli_df;
            } else if (vm.pro_send.includes(vm.data[item].product_name) == 1) {
              vm.ind.push(vm.data[item].industry_type);
              vm.cli.push(vm.data[item].client_name);
            }
          }

          this.allSelected_pro = false;
          this.changeFilter();
          break;         
      }
    }, //clickFilter end  for  ind cli pro
    clickAll(value) {
      const vm = this;
      switch (value) {
        case "ind":
          vm.ind_send = [];
          if (vm.allSelected_ind) {
            vm.indFD.forEach(function (item) {
              vm.ind_send.push(item);
            });
          }
          vm.clickFilter("ind");
          break;
        case "cli":
          vm.cli_send = [];
          if (vm.allSelected_cli) {
            vm.cliFD.forEach(function (item) {
              vm.cli_send.push(item);
            });
          }
          vm.clickFilter("cli");
          break;
        case "pro":
          vm.pro_send = [];
          if (vm.allSelected_pro) {
            vm.proFD.forEach(function (item) {
              vm.pro_send.push(item);
            });
          }
          vm.clickFilter("pro");
          break;
        case "paymethod":
          vm.paymethod_send = [];

          if (vm.allSelected_paymethod) {
            vm.paymethodFD.forEach(function (item) {
              vm.paymethod_send.push(item.value);
            });
          }
          break;
        case "ordertype":
          vm.ordertype_send = [];
          if (vm.allSelected_ordertype) {
            vm.ordertypeFD.forEach(function (item) {
              vm.ordertype_send.push(item);
            });
          }
          break;
        case "regular":
          vm.regular_send = [];
          if (vm.allSelected_regular) {
            vm.regularFD.forEach(function (item) {
              vm.regular_send.push(item);
            });
          }
          break;
        case "campaign":
          vm.campaign_send = [];
          if (vm.allSelected_campaign) {
            vm.campaignFD.forEach(function (item) {
              vm.campaign_send.push(item);
            });
          }
          break;
        case "link_type":
          vm.line_type_send = [];
          if (vm.allSelected_link_type) {
            vm.link_type_FD.forEach(function (item) {
              vm.link_type_send.push(item);
            });
          }
          break;
        case "media":
          vm.media_send = [];
          if (vm.allSelected_media) {
            vm.mediaFD.forEach(function (item) {
              vm.media_send.push(item);
            });
          }
          break;
        case "orderstatus":
          vm.orderstatus_send = [];
          if (vm.allSelected_orderstatus) {
            vm.orderstatusFD.forEach(function (item) {
              vm.orderstatus_send.push(item);
            });
          }
          break;
        case "orderRoute":
          vm.orderRoute_send = [];
          if (vm.allSelected_orderRoute) {
            vm.orderRouteFD.forEach(function (item) {
              vm.orderRoute_send.push(item);
            });
          }
          break;

        case "ga1":
          vm.ga1_send = [];
          if (vm.allSelected_ga1) {
            vm.ga1FD.forEach(function (item) {
              vm.ga1_send.push(item);
            });
          }
          break;
        case "ga2":
          vm.ga2_send = [];
          if (vm.allSelected_ga2) {
            vm.ga2FD.forEach(function (item) {
              vm.ga2_send.push(item);
            });
          }
          break;
        case "ga3":
          vm.ga3_send = [];
          if (vm.allSelected_ga3) {
            vm.ga3FD.forEach(function (item) {
              vm.ga3_send.push(item);
            });
          }
          break;
      }
    }, // clickAll
    clickClear(value) {
      const vm = this;
      switch (value) {
        case "ind":
          vm.ind_send = [];
          vm.allSelected_ind = false;
          vm.clickFilter("ind");
          break;
        case "cli":
          vm.cli_send = [];
          vm.allSelected_cli = false;
          vm.clickFilter("cli");
          break;
        case "pro":
          vm.pro_send = [];
          vm.allSelected_pro = false;
          vm.clickFilter("pro");
          break;
        case "paymethod":
          vm.paymethod_send = [];
          vm.allSelected_paymethod = false;
          break;
        case "ordertype":
          vm.ordertype_send = [];
          vm.allSelected_ordertype = false;
          break;
        case "regular":
          vm.regular_send = [];
          vm.allSelected_regular = false;
          break;
        case "campaign":
          vm.campaign_send = [];
          vm.allSelected_campaign = false;
          break;
        case "media":
          vm.media_send = [];
          vm.allSelected_media = false;
          break;
        case "link_type":
          vm.link_type_send = [];
          vm.allSelected_link_type = false;
          break;
        case "orderstatus":
          vm.orderstatus_send = [];
          vm.allSelected_orderstatus = false;
          break;
        case "orderRoute":
          vm.orderRoute_send = [];
          vm.allSelected_orderRoute = false;
          break;
        case "ga1":
          vm.ga1_send = [];
          vm.allSelected_ga1 = false;
          break;
        case "ga2":
          vm.ga2_send = [];
          vm.allSelected_ga2 = false;
          break;
        case "ga3":
          vm.ga3_send = [];
          vm.allSelected_ga3 = false;
          break;
      }
    }, // clickClear
  },
  watch: {
    $route() {
      this.nowstep = "one";
    }, //前面只要切換葉面 就會回到第一頁
  },
  created() {
    const vm = this;

    $.ajax({
      type: "GET",
      url: `http://192.168.111.166:8070/client/data`, //本地端
      dataType: "json",
      success: function (response) {
        vm.data = response;
        const newind = response.map((item) => item.industry_type);
        const newcli = response.map((item) => item.client_name);
        const newpro = response.map((item) => item.product_name);
        vm.ind = newind.filter((item, key, arr) => arr.indexOf(item) == key);
        vm.cli = newcli.filter((item, key, arr) => arr.indexOf(item) == key);
        vm.pro = newpro.filter((item, key, arr) => arr.indexOf(item) == key);
        vm.ind_df = newind.filter((item, key, arr) => arr.indexOf(item) == key);
        vm.cli_df = newcli.filter((item, key, arr) => arr.indexOf(item) == key);
        vm.pro_df = newpro.filter((item, key, arr) => arr.indexOf(item) == key);
      },
    });
  },
};
</script>




