                @click="Description = 'cardCVR'"
              )
            i.fas.fa-eye(@click="$chart_scale('cardCVR')")
            .spinner-border.spinner-border-sm.chart_loading(v-if="isLoading")
            canvas#chartjsLineCVR 
  .row 
    .col-xl-6.grid-margin.stretch-card
      .col-sm-12.ml-3
        .card.cardCPC
          .card-body
            h6.card-title CPC
              i.fas.fa-question.ml-3(
                type="button",
                data-toggle="modal",
                data-target="#exampleModal",
                @click="Description = 'cardCPC'"
              )
            i.fas.fa-eye(@click="$chart_scale('cardCPC')")
            .spinner-border.spinner-border-sm.chart_loading(v-if="isLoading")
            canvas#chartjsLineCPC 
    .col-xl-6.grid-margin.stretch-card
      .col-sm-12.ml-3
        .card.cardCPM
          .card-body
            h6.card-title CPM
              i.fas.fa-question.ml-3(
                type="button",
                data-toggle="modal",
                data-target="#exampleModal",
                @click="Description = 'cardCPM'"
              )
            i.fas.fa-eye(@click="$chart_scale('cardCPM')")
            .spinner-border.spinner-border-sm.chart_loading(v-if="isLoading")
            canvas#chartjsLineCPM 
  .row 
    .col-xl-6.grid-margin.stretch-card
      .col-sm-12.ml-3
        .card.cardCPO
          .card-body
            h6.card-title CPO
              i.fas.fa-question.ml-3(
                type="button",
                data-toggle="modal",
                data-target="#exampleModal",
                @click="Description = 'cardCPO'"
              )
            i.fas.fa-eye(@click="$chart_scale('cardCPO')")
            .spinner-border.spinner-border-sm.chart_loading(v-if="isLoading")
            canvas#chartjsLineCPO 
    .col-xl-6.grid-margin.stretch-card(v-show="head.length == 1")
      .col-sm-12.ml-3
        .card.cardCOST_CPO
          .card-body
            h6.card-title COST x CPO
              i.fas.fa-question.ml-3(
                type="button",
                data-toggle="modal",
                data-target="#exampleModal",
                @click="Description = 'cardCOSTCPO'"
              )
            i.fas.fa-eye(@click="$chart_scale('cardCOST_CPO')")
            .spinner-border.spinner-border-sm.chart_loading(v-if="isLoading")
            canvas#chartjsLineCOST_CPO 
  .row
    .col-xl-6.grid-margin.stretch-card(v-show="head.length == 1")
      .col-sm-12.ml-3
        .card.cardCPO_CV
          .card-body
            h6.card-title CPO x CV
              i.fas.fa-question.ml-3(
                type="button",
                data-toggle="modal",
                data-target="#exampleModal",
                @click="Description = 'cardCPOCV'"
              )
            i.fas.fa-eye(@click="$chart_scale('cardCPO_CV')")
            .spinner-border.spinner-border-sm.chart_loading(v-if="isLoading")
            canvas#chartjsLineCPO_CV
    .col-xl-6.grid-margin.stretch-card(v-show="head.length == 1")
      .col-sm-12.ml-3
        .card.cardCPO_CPM
          .card-body
