              )
            i.fas.fa-eye(@click="$chart_scale('cardCTR_CVR')")
            .spinner-border.spinner-border-sm.chart_loading(v-if="isLoading")
            canvas#chartjsLineCTR_CVR
    .col-xl-6.grid-margin.stretch-card(v-show="head.length == 1")
      .col-sm-12.ml-3
        .card.cardCPM_CVR
          .card-body
            h6.card-title CPM x CVR
              i.fas.fa-question.ml-3(
                type="button",
                data-toggle="modal",
                data-target="#exampleModal",
                @click="Description = 'cardCPMCVR'"
              )
            i.fas.fa-eye(@click="$chart_scale('cardCPM_CVR')")
            .spinner-border.spinner-border-sm.chart_loading(v-if="isLoading")
            canvas#chartjsLineCPM_CVR
  .row 
    .col-xl-6.grid-margin.stretch-card(v-show="head.length == 1")
      .col-sm-12.ml-3
        .card.cardCPM_CTR_CVR
          .card-body
            h6.card-title CPM x CTR x CVR
              i.fas.fa-question.ml-3(
                type="button",
                data-toggle="modal",
                data-target="#exampleModal",
                @click="Description = 'cardCPMCTRCVR'"
              )
            i.fas.fa-eye(@click="$chart_scale('cardCPM_CTR_CVR')")
            .spinner-border.spinner-border-sm.chart_loading(v-if="isLoading")
            canvas#chartjsLineCPM_CTR_CVR 
  #exampleModal.modal.fade(
    tabindex="-1",
    role="dialog",
    aria-labelledby="exampleModalLabel",
    aria-hidden="true"
  ) <!-- ?號 的提示盒子 -->
    .modal-dialog.modal-dialog-centered(
      role="document",
      v-show="Description == item.name",
      v-for="item in Description_context"
    )
      .modal-content
        .modal-header
          h5#exampleModalLabel.modal-title {{ item.title }}
          button.close(type="button", data-dismiss="modal", aria-label="Close")
            span(aria-hidden="true") &times;
        .modal-body <!-- 盒子內容 -->
          span(v-html="item.context1") 
          span(v-html="item.context2") 
          span(v-html="item.context3")
          span(v-html="item.context4")
          span(v-html="item.context5")
          span(v-html="item.context6")
          span(v-html="item.context7")
          span(v-html="item.context8")
          span(v-html="item.context9") 

        .modal-footer
          button.btn.btn-secondary(type="button", data-dismiss="modal") Close
  keep-alive 
    LINETABLE(:line="line_df", v-if="sendNow", :head="head") <!-- 表格 -->
</template>

<script>
import $ from "jquery";
import LINETABLE from "@/components/table/line-table";
export default {
  components: {
    LINETABLE,
  },
  data() {
    return {
      line_df: [], // 表格
      head: [], // 表格
      sendNow: false, // 表格狀態
      Description: "",
      Description_context: [
        // 提示框的內容
        {
          title: "CPO",
          name: "cardCPO",
          context1: `Data Source：媒体管理画面、素材報告 <br>`,
          context2: "Formula：【Cost】 / 【CV】",
        },
        {
          title: "CV",
          name: "cardCV",
          context1: `Data Source：媒体管理画面、素材報告 <br>`,
          context2: "Data Column：【網站購買】",
        },
        {
          title: "COST",
          name: "cardCOST",
          context1: `Data Source：媒体管理画面、素材報告 <br>`,
          context2: "Data Column：【花費金額 (TWD)】",
        },
        {
          title: "CVR",
          name: "cardCVR",
          context1: `Data Source：媒体管理画面、素材報告 <br>`,
          context2: `Formula：【CV】 / 【Click】 <br>`,
          context3: "Format：%",
        },
        {
          title: "CTR",
          name: "cardCTR",
          context1: `Data Source：媒体管理画面、素材報告 <br>`,
          context2: `Formula：【Click】 / 【IMP】<br>`,
          context3: "Format：%",
        },
        {
          title: "CPM",
          name: "cardCPM",
          context1: `Data Source：媒体管理画面、素材報告 <br>`,
          context2: "Formula：【Cost】 / 【IMP】 * 1000",
        },
        {
          title: "CPC",
          name: "cardCPC",
          context1: `Data Source：媒体管理画面、素材報告 <br>`,
          context2: "Formula：【Cost】 / 【Click】",
        },
        {
          title: "IMP",
          name: "cardIMP",
          context1: `Data Source：媒体管理画面、素材報告 <br>`,
          context2: "Data Column：【曝光次數】",
        },
        {
          title: "CLICK",
          name: "cardCLICK",
          context1: `Data Source：媒体管理画面、素材報告 <br>`,
          context2: "Data Column：【連結點擊次數】",
        },
        {
          title: "COST x CPO",
          name: "cardCOSTCPO",
          context1: `&nbsp;&nbsp; ● COST <br>`,
          context2: ` &nbsp;&nbsp;&nbsp;&nbsp; &nbsp;&nbsp; Data Source：媒体管理画面、素材報告 <br>`,
          context3: `&nbsp;&nbsp;&nbsp;&nbsp; &nbsp;&nbsp; Data Column：【花費金額 (TWD)】 <br>`,
          context4: `&nbsp;&nbsp; ● CPO <br>`,
          context5: `&nbsp;&nbsp;&nbsp;&nbsp; &nbsp;&nbsp; Data Source：媒体管理画面、素材報告 <br>`,
          context6: `&nbsp;&nbsp;&nbsp;&nbsp; &nbsp;&nbsp;  Formula：【Cost】 / 【CV】`,
        },
        {
          title: "CPO x CV",
          name: "cardCPOCV",
          context1: `&nbsp;&nbsp; ● CPO <br>`,
          context2: ` &nbsp;&nbsp;&nbsp;&nbsp; &nbsp;&nbsp; Data Source：媒体管理画面、素材報告 <br>`,
          context3: `&nbsp;&nbsp;&nbsp;&nbsp; &nbsp;&nbsp; Formula：【Cost】 / 【CV】 <br>`,
          context4: `&nbsp;&nbsp; ● CV <br>`,
          context5: `&nbsp;&nbsp;&nbsp;&nbsp; &nbsp;&nbsp; Data Source：媒体管理画面、素材報告 <br>`,
          context6: `&nbsp;&nbsp;&nbsp;&nbsp; &nbsp;&nbsp;  Data Column：【網站購買】`,
        },
        {
          title: "CPO x CPM",
          name: "cardCPOCPM",
          context1: `&nbsp;&nbsp; ● CPO <br>`,
          context2: ` &nbsp;&nbsp;&nbsp;&nbsp; &nbsp;&nbsp; Data Source：媒体管理画面、素材報告 <br>`,
          context3: `&nbsp;&nbsp;&nbsp;&nbsp; &nbsp;&nbsp; Formula：【Cost】 / 【CV】 <br>`,
          context4: `&nbsp;&nbsp; ● CPM <br>`,
          context5: `&nbsp;&nbsp;&nbsp;&nbsp; &nbsp;&nbsp; Data Source：媒体管理画面、素材報告 <br>`,
          context6: `&nbsp;&nbsp;&nbsp;&nbsp; &nbsp;&nbsp;  Formula：【Cost】 / 【IMP】 * 1000`,
        },
        {
          title: "CTR x CVR",
          name: "cardCTRCVR",
          context1: `&nbsp;&nbsp; ● CTR <br>`,
          context2: ` &nbsp;&nbsp;&nbsp;&nbsp; &nbsp;&nbsp; Data Source：媒体管理画面、素材報告 <br>`,
          context3: `&nbsp;&nbsp;&nbsp;&nbsp; &nbsp;&nbsp; Formula：【Click】 / 【IMP】 <br> &nbsp;&nbsp;&nbsp;&nbsp; &nbsp;&nbsp; Format：% <br> `,
          context4: `&nbsp;&nbsp; ● CPM <br>`,
          context5: `&nbsp;&nbsp;&nbsp;&nbsp; &nbsp;&nbsp; Data Source：媒体管理画面、素材報告 <br>`,
          context6: `&nbsp;&nbsp;&nbsp;&nbsp; &nbsp;&nbsp;  Formula：【Cost】 / 【IMP】 * 1000`,
        },
        {
          title: "CPM x CVR",
          name: "cardCPMCVR",
          context1: `&nbsp;&nbsp; ● CPM <br>`,
          context2: ` &nbsp;&nbsp;&nbsp;&nbsp; &nbsp;&nbsp; Data Source：媒体管理画面、素材報告 <br>`,
          context3: `&nbsp;&nbsp;&nbsp;&nbsp; &nbsp;&nbsp; Formula：【Cost】 / 【IMP】 * 1000 <br>`,
          context4: `&nbsp;&nbsp; ● CVR <br>`,
          context5: `&nbsp;&nbsp;&nbsp;&nbsp; &nbsp;&nbsp; Data Source：媒体管理画面、素材報告 <br>`,
          context6: `&nbsp;&nbsp;&nbsp;&nbsp; &nbsp;&nbsp;  Formula：【CV】 / 【Click】<br> &nbsp;&nbsp;&nbsp;&nbsp; &nbsp;&nbsp; Format：% `,
        },
        {
          title: "CPM x CTR x CVR",
          name: "cardCPMCTRCVR",
          context1: `&nbsp;&nbsp; ● CPM <br>`,
          context2: ` &nbsp;&nbsp;&nbsp;&nbsp; &nbsp;&nbsp; Data Source：媒体管理画面、素材報告 <br>`,
          context3: `&nbsp;&nbsp;&nbsp;&nbsp; &nbsp;&nbsp; Formula：【Click】 / 【IMP】 <br>`,
          context4: `&nbsp;&nbsp; ● CTR <br>`,
          context5: `&nbsp;&nbsp;&nbsp;&nbsp; &nbsp;&nbsp; Data Source：媒体管理画面、素材報告 <br>`,
          context6: `&nbsp;&nbsp;&nbsp;&nbsp; &nbsp;&nbsp;  Formula：【Click】 / 【IMP】 <br> &nbsp;&nbsp;&nbsp;&nbsp; &nbsp;&nbsp; Format：% <br> `,
          context7: `&nbsp;&nbsp; ● CVR <br>`,
          context8:
            "&nbsp;&nbsp;&nbsp;&nbsp; &nbsp;&nbsp; Data Source：媒体管理画面、素材報告 <br>",
          context9: `&nbsp;&nbsp;&nbsp;&nbsp; &nbsp;&nbsp;  Formula：【CV】 / 【Click】<br> &nbsp;&nbsp;&nbsp;&nbsp; &nbsp;&nbsp; Format：% `,
        },
      ],
      //圖表盒子變數
      a_CPO: new_cpo,
      a_CV: new_cv,
      a_COST: new_cost,
      a_CVR: new_cvr,
      a_CTR: new_ctr,
      a_CPM: new_cpm,
      a_CPC: new_cpc,
      a_IMP: new_imp,
      a_CLICK: new_click,
      a_COST_CPO: new_COST_CPO,
      a_CPO_CV: new_CPO_CV,
      a_CPO_CPM: new_CPO_CPM,
      a_CTR_CVR: new_CTR_CVR,
      a_CPM_CVR: new_CPM_CVR,
      a_CPM_CTR_CVR: new_CPM_CTR_CVR,
    };
  },
  watch: {
    dateType() {
      const vm = this;
      vm.upDate();
    },
    new_data() {
      this.upDate();
    },
    nowHead() {
      this.upDate();
    },
  },
  computed: {
    isLoading() {
      return this.$store.state.line.isLoading; // loading 變數獲取
    },
    new_data() {
      return this.$store.state.line.data; //   獲取當前 line data
    },
    dateType() {
      return this.$store.state.line.dateType; // 獲取當前date 類別
    },
    nowHead() {
      return this.$store.state.line.head; // 獲取當前指標值
    },
  },
  methods: {
    upDate() {
      // 圖表的update

      const vm = this;
      let line_df = this.$store.state.line.data; // cros的資料來源
      let new_line = [];
      // 判斷當前時間類別
      if (this.$store.state.line.head == "cli") {
        vm.head = line_df[0].clients;
      }
      if (this.$store.state.line.head == "ind") {
        vm.head = line_df[0].industrytype;
      }
      if (this.$store.state.line.head == "pro") {
        vm.head = line_df[0].products;
      }

      vm.line_df = line_df[1];
      
      if (!vm.dateType) {
        line_df = line_df[1];
      }
      if (vm.dateType == "m") {
        line_df = line_df[2];
      }
      if (vm.dateType == "q") {
        line_df = line_df[3];
      }

      vm.sendNow = true;
      let timeBox = [];
      let line_charts = [[], [], [], [], [], [], [], [], []]; // 預設產生圖表
      // 圖表顏色
      let color_all = [
        "#f38181",
        "#fce38a",
        "#eaffd0",
        "#a8e6cf",
        "#fdffab",
        "#ffd3b6",
        "#ffaaa5",
        "#d4a5a5",
        "#ffecda",
        "#f9ffea",
        "#a6d0e4",
        "#ff6f3c",
        "#ff9a3c",
        "#ffc93c",
        "#17b978",
        "#a7ff83",
      ];
      // 依據當前客戶數產生陣列與預設資料
      for (let head in vm.head) {
        for (let charts in line_charts) {
          line_charts[charts].push({
            data: [],
            label: vm.head[head],
            borderColor: color_all[head] || "#7ee5e5",
            backgroundColor: "#fff",
            fill: false,
          });
        }
        let [
          cpoBox,
          cvBox,
          costBox,
          cvrBox,
          ctrBox,
          cpmBox,
          cpcBox,
          impBox,
          clickBox,
        ] = line_charts;

        // 如果指標是客戶別
        if (this.$store.state.line.head == "cli") {
          clientname();
          filterData();
        }
        // 如果指標是產業別
        if (this.$store.state.line.head == "ind") {
          industrytype();
          filterData();
        }
        if (this.$store.state.line.head == "pro") {
          productname();
        }

        //客戶別函數
        function clientname() {
          line_df.forEach(function (item) {
            if (item.clientname.includes(vm.head[head]) == 1)
              timeBox.push(item.date);

            if (item.clientname == cpoBox[head].label)
              cpoBox[head].data.push({
                x: item.date,
                y: 0,
              });
            if (item.clientname == cvBox[head].label)
              cvBox[head].data.push({
                x: item.date,
                y: item.cv,
              });
            if (item.clientname == costBox[head].label)
              costBox[head].data.push({
                x: item.date,
                y: Math.round(item.cost * 100) / 100,
              });
            if (item.clientname == cvrBox[head].label)
              cvrBox[head].data.push({
                x: item.date,
                y: 0,
              });
            if (item.clientname == ctrBox[head].label)
              ctrBox[head].data.push({
                x: item.date,
                y: 0,
              });
            if (item.clientname == cpmBox[head].label)
              cpmBox[head].data.push({
                x: item.date,
                y: 0,
              });
            if (item.clientname == cpcBox[head].label)
              cpcBox[head].data.push({
                x: item.date,
                y: 0,
              });
            if (item.clientname == impBox[head].label)
              impBox[head].data.push({
                x: item.date,
                y: item.impressions,
              });
            if (item.clientname == clickBox[head].label)
              clickBox[head].data.push({
                x: item.date,
                y: item.clicks,
              });
          });
        }
        //產業別涵式
        function industrytype() {
          line_df.forEach(function (item) {
            if (item.industrytype.includes(vm.head[head]) == 1) {
              timeBox.push(item.date);
            }
            if (item.industrytype == cpoBox[head].label) {
              cpoBox[head].data.push({
                x: item.date,
                y: "",
              });
            }
            if (item.industrytype == cvBox[head].label) {
              cvBox[head].data.push({
                x: item.date,
                y: item.cv,
              });
            }
            if (item.industrytype == costBox[head].label) {
              costBox[head].data.push({
                x: item.date,
                y: Math.round(item.cost * 100) / 100,
              });
            }

            if (item.industrytype == cvrBox[head].label) {
              cvrBox[head].data.push({
                x: item.date,
                y: 0,
              });
            }

            if (item.industrytype == ctrBox[head].label) {
              ctrBox[head].data.push({
                x: item.date,
                y: "",
              });
            }

            if (item.industrytype == cpmBox[head].label) {
              cpmBox[head].data.push({
                x: item.date,
                y: "",
              });
            }

            if (item.industrytype == cpcBox[head].label) {
              cpcBox[head].data.push({
                x: item.date,
                y: "",
              });
            }

            if (item.industrytype == impBox[head].label) {
              impBox[head].data.push({
                x: item.date,
                y: item.impressions,
              });
            }

            if (item.industrytype == clickBox[head].label) {
              clickBox[head].data.push({
                x: item.date,
                y: item.clicks,
              });
            }
          });
        }

        //產品名稱
        function productname() {
          line_df.forEach(function (item) {
            if (item.productname.includes(vm.head[head]) == 1)
              timeBox.push(item.date);
            if (item.productname == cpoBox[head].label)
              cpoBox[head].data.push({
                x: item.date,
                y: Math.round(item.cpo * 100) / 100,
              });
            if (item.productname == cvBox[head].label)
              cvBox[head].data.push({
                x: item.date,
                y: item.cv,
              });
            if (item.productname == costBox[head].label)
              costBox[head].data.push({
                x: item.date,
                y: Math.round(item.cost * 100) / 100,
              });
            if (item.productname == cvrBox[head].label)
              cvrBox[head].data.push({
                x: item.date,
                y: Math.round(item.cvr * 100) / 100,
              });
            if (item.productname == ctrBox[head].label)
              ctrBox[head].data.push({
                x: item.date,
                y: Math.round(item.ctr * 100) / 100,
              });
            if (item.productname == cpmBox[head].label)
              cpmBox[head].data.push({
                x: item.date,
                y: Math.round(item.cpm * 100) / 100,
              });
            if (item.productname == cpcBox[head].label)
              cpcBox[head].data.push({
                x: item.date,
                y: Math.round(item.cpc * 100) / 100,
              });
            if (item.productname == impBox[head].label)
              impBox[head].data.push({
                x: item.date,
                y: item.impressions,
              });
            if (item.productname == clickBox[head].label)
              clickBox[head].data.push({
                x: item.date,
                y: item.clicks,
              });
          });
        }

        // 依照指標先行篩選
        function filterData() {
          //通用時間
          let df_time = cvBox[head].data.map((item) => item.x); // 設定日期
          
          // cvBox
          let newCv = []; //盒子
          let newCv_y = cvBox[head].data.map((item) => item.y); //給值
         
          
          
          newCv = df_time.reduce(function (a, b, i) {
            if (!a.hasOwnProperty(b)) {
              a[b] = 0;
            }
            a[b] += Number(newCv_y[i]);
            return a;
          }, {});
          let cv_val = Object.values(newCv);
          let time_lab = Object.keys(newCv);  // 篩選過的通用時間  
          cvBox[head].data = cv_val; // 給值
          
          // imp box
          let newimp = []; //盒子
          let newimp_y = []; //值
         
          newimp_y = impBox[head].data.map((item) => item.y); //給值
          newimp =  df_time.reduce(function (a, b, i) {
            if (!a.hasOwnProperty(b)) {
              a[b] = 0;
            }
            a[b] += Number(newimp_y[i]);
            return a;
          }, {});
          let imp_val = Object.values(newimp);
          impBox[head].data = imp_val; // 給值

          // clickBox
          let newclick = []; //盒子
          let newclick_y = clickBox[head].data.map((item) => item.y); //給值
    
      
         
          newclick =  df_time.reduce(function (a, b, i) {
            if (!a.hasOwnProperty(b)) {
              a[b] = 0;
            }
            a[b] += Number(newclick_y[i]);
            return a;
          }, {});
          let click_val = Object.values(newclick);
          clickBox[head].data = click_val; // 給值

          //costBox
          let newcost = []; //盒子
          let newcost_y = []; //值
         
          newcost_y = costBox[head].data.map((item) => item.y); //給值
          newcost =  df_time.reduce(function (a, b, i) {
            if (!a.hasOwnProperty(b)) {
              a[b] = 0;
            }
            
            a[b] = Number(newcost_y[i]);
            return a;
          }, {});
          let cost_val = Object.values(newcost);
          costBox[head].data = cost_val; // 給值

          //cvrBox
          let newcvr = []; //盒子
          let newcvr_y = []; //值
          newcvr_y = cvrBox[head].data.map((item) => item.y); //給值
          
          // 因為是相除所以必須是利用篩選過的時間
          newcvr = time_lab.reduce(function(a,b,i){
            if (!a.hasOwnProperty(b)) {
              a[b] = 0;
              
            }
            a[b] += Math.round((cv_val[i]/click_val[i]) * 100) / 100;
            
            return a;
          },{})

          
           let cvr_val = Object.values(newcvr);
           cvrBox[head].data = cvr_val; // 給值

          //ctrBox
          let newctr = []; //盒子
          let newctr_y = []; //值
         
          newctr_y = ctrBox[head].data.map((item) => item.y); //給值
          newctr = time_lab.reduce(function (a, b, i) {
            if (!a.hasOwnProperty(b)) {
              a[b] = 0;
            }
            a[b] += Number( Math.round((click_val[i] / imp_val[i]) * 100) / 100);
            
            return a;
          }, {});
          let ctr_val = Object.values(newctr);
         

          ctrBox[head].data = ctr_val; // 給值

          //cpcBox

          let newcpc = []; //盒子
          let newcpc_y = []; //值
          newcpc_y = cpcBox[head].data.map((item) => item.y); //給值
          
          newcpc = time_lab.reduce(function (a, b, i) {
            if (!a.hasOwnProperty(b)) {
              a[b] = 0;
            }
            a[b] = Number(
              Math.round((cost_val[i] / click_val[i]) * 100) / 100
            );
            return a;
          }, {});
          let cpc_val = Object.values(newcpc);
          cpcBox[head].data = cpc_val; // 給值

          //cpmBox

          let newcpm = []; //盒子
          let newcpm_y = []; //值
          newcpm_y = cpmBox[head].data.map((item) => item.y); //給值
         
          newcpm =time_lab.reduce(function (a, b, i) {
            if (!a.hasOwnProperty(b)) {
              a[b] = 0;
            }
            a[b] += Number(
              Math.round((cost_val[i] / imp_val[i]) * 1000 * 100) / 100
            );
            return a;
          }, {});
          let cpm_val = Object.values(newcpm);
          cpmBox[head].data = cpm_val; // 給值

          //cpoBox

          let newcpo = []; //盒子
          let newcpo_y = []; //值
          newcpo_y = cpoBox[head].data.map((item) => item.y); //給值
          
          newcpo = time_lab.reduce(function (a, b, i) {
            if (!a.hasOwnProperty(b)) {
              a[b] = 0;
            }
            a[b] += Number(Math.round((cost_val[i] / cv_val[i]) * 100) / 100);
            return a;
          }, {});

          let cpo_val = Object.values(newcpo);

          cpoBox[head].data = cpo_val; // 給值
        } // filterData end

        // 時間排序
        timeBox = timeBox.filter((item, key, arr) => arr.indexOf(item) == key);
        timeBox.sort((a, b) => (a > b ? 1 : -1)); //
        // time
        vm.a_COST_CPO.datasets[0].data = costBox[0].data; // COST
        vm.a_COST_CPO.datasets[1].data = cpoBox[0].data; // Cpo

        vm.a_CPO_CV.datasets[0].data = cpoBox[0].data; // Cpo
        vm.a_CPO_CV.datasets[1].data = cvBox[0].data; // Cv

        vm.a_CPO_CPM.datasets[0].data = cpoBox[0].data; // Cpo
        vm.a_CPO_CPM.datasets[1].data = cpmBox[0].data; // cpm

        vm.a_CTR_CVR.datasets[0].data = ctrBox[0].data; // Ctr
        vm.a_CTR_CVR.datasets[1].data = cvrBox[0].data; // cvr

        vm.a_CPM_CVR.datasets[0].data = cpmBox[0].data; // cpm
        vm.a_CPM_CVR.datasets[1].data = cvrBox[0].data; // cvr

        vm.a_CPM_CTR_CVR.datasets[0].data = cpmBox[0].data; // cpm
        vm.a_CPM_CTR_CVR.datasets[1].data = ctrBox[0].data; // ctr
        vm.a_CPM_CTR_CVR.datasets[2].data = cvrBox[0].data; // cvr
        // 給入圖表
        vm.a_CPO.datasets = cpoBox;
        vm.a_CV.datasets = cvBox;
        vm.a_COST.datasets = costBox;
        vm.a_CVR.datasets = cvrBox;

        vm.a_CTR.datasets = ctrBox;
        vm.a_CPM.datasets = cpmBox;
        vm.a_CPC.datasets = cpcBox;
        vm.a_IMP.datasets = impBox;
        vm.a_CLICK.datasets = clickBox;
      }
      //給入時間
      vm.a_CPO.labels = timeBox;
      vm.a_CV.labels = timeBox;
      vm.a_COST.labels = timeBox;
      vm.a_CVR.labels = timeBox;
      vm.a_CTR.labels = timeBox;
      vm.a_CPM.labels = timeBox;
      vm.a_CPC.labels = timeBox;
      vm.a_IMP.labels = timeBox;
      vm.a_CLICK.labels = timeBox;
      vm.a_COST_CPO.labels = timeBox;
      vm.a_CPO_CV.labels = timeBox;
      vm.a_CPO_CPM.labels = timeBox;
      vm.a_CTR_CVR.labels = timeBox;
      vm.a_CPM_CVR.labels = timeBox;
      vm.a_CPM_CTR_CVR.labels = timeBox;

      //更新
      window.myBarCPO.update();
      window.myBarCV.update();
      window.myBarCOST.update();
      window.myBarCVR.update();

      window.myBarCTR.update();
      window.myBarCPM.update();
      window.myBarCPC.update();
      window.myBarIMP.update();
      window.myBarCLICK.update();
      window.myBarCOST_CPO.update();
      window.myBarCPO_CV.update();
      window.myBarCPO_CPM.update();
      window.myBarCTR_CVR.update();
      window.myBarCPM_CVR.update();
      window.myBarCPM_CTR_CVR.update();
      console.timeEnd("line updata的執行時間");
    },
  },
