<template lang="pug">
    nav.sidebar
        .sidebar-header
            a.sidebar-brand(href='#') STA可視化
               span.ml-1 
                i.far.fa-star 
            .sidebar-toggler.not-active(@click="menu")
                    span
                    span
                    span
        .sidebar-body
            ul.nav  <!-- 選單列 -->
                li.nav-item.nav-category 営業
                li.nav-item(v-for="item in business" ) 
                    router-link(:to="item.link" ).nav-link
                        span(:class="{ 'active' : $route.name.includes(item.page) }") <!-- 換頁 -->
                            i(:class="item.class") 
                            span.link-title {{item.name}} 
                li.nav-item.nav-category  media
                 // メディア
                li.nav-item(v-for="item in media" ) 
                    router-link(:to="item.link" ).nav-link
                        span(:class="{ 'active' : $route.name.includes(item.page) }")
                            i(:class="item.class" ) 
                            span.link-title  {{item.name}}                                 
                                   
</template>

<script>
export default {
     name: "sidebar",
     data(){ //菜單資料與顯示
       return{
          business: [
              {name:"CROS",class:"fab fa-cuttlefish",link:"/" ,page: "cros"},
          ],
          media:[   
              {name:"FACEBOOK",class:"fab fa-facebook",link:"/fb-page",page:"fb-page"},
              {name:"LINE",class:"fab fa-line",link:"/line-page",page:"line-page"}
          ] 
       }  
     },
     methods:{
         menu(){ // 摺疊時的樣式
             $(".active").css('bacground','none');
         }
     }
     

};
</script>
<style scoped lang="scss">
  nav.sidebar{
       z-index: 100000000000000000;
      .sidebar-toggler.not-active{
          cursor: url('/static/images/click.ico') ,auto ;
          span{
                cursor: url('/static/images/click.ico') ,auto ;
          }

      }
      .sidebar-header{
         background:#30394D;
        border-bottom: #000;
       
      }
      .sidebar-body{
        background:#30394D;
  
           ul{
               width: 100%;
           }
      }
      .nav-link{
          cursor: url('/static/images/click.ico') ,auto !important;
      }
      .nav-item{
          color:#fff;
          display: block;
          margin-top: 0.8em;
          
          span ,i{
             font-size: 1em;
             cursor: url('/static/images/click.ico') ,auto !important;
          }
      } 
      span.active{
        background: #fff;
        color: #000;
        padding: 10px;
        width: 100%;
        border-radius: 5px;
        display: flex;
       
        i{
            font-size: 1.5em;
            opacity: 1; 
         
        }
     
        
      }
     

  }
    
     
</style>



