<template lang="pug">
.dorento
  ul.tag-pagination.nav.tag-cros
    a(:href="download_api", download, v-show="!isloading") <!-- loading -->
      img.download(src="/static/images/download.svg", @click="download_loading") 
    a(v-show="isloading") <!-- loading 執行中 -->
      .spinner-border.spinner-border-sm.download 
  transition(name="page", mode="out-in")
    keep-alive
      router-view(:key="$route.path") <!-- cros頁面 -->
</template>

<script>
export default {
  data() {
    return {
      isloading: false, //loading 變數
    };
  },
  computed: {
    download_api() {
      return this.$store.state.cros.download_api; //預設下載api取得
    },
  },
  methods: {
    download_loading() {
      // 下載api
      this.isloading = !this.isloading;
      let api = this.download_api;
      this.$http.get(api).then((res) => {
        if (res) {
          this.isloading = !this.isloading;
          alert("下載成功");
        } else {
          return;
        }
      });
    },
  },
 
};
</script>




