import axios from "axios"; //主要的ajax套件

export default {
  namespaced: true, //區域變數打開
  state: {
    isLoading: false, //cros頁面的loading
    isLoading_filter: false,
    data: [], // 資料包
    margin: 0, //
    cli: [], //  客戶
    pro: [], //   產品
    pay: [], //   支付方式
    ord: [], //   新規 / 既存
    reg: [], //   定期 / 都度
    cam: [], //   キャンペーン名
    media: [], //  媒體類別
    link: [], //リンク別
    ord_s: [],
    ord_r: [],
    del_f: [],
    ga1: [], //ga1
    ga2: [], // ga2
    ga3: [], //ga3
    mode: "", // 篩選參數 與當前頁面
    date1: "", // 篩選參數 時間別
    date2: "", // 篩選參數 時間別
    dateType: "", // 判定年月日
    send: 0, //送出判別
    download_api: "" //下載的參數
  },
  actions: {
    timeType(context, type) {
      //時間型態
      context.commit("sendDateType", type);
    },
    getTime(context, { date1, date2, margin }) {
      //改變時間
      context.commit("sendDate", { date1, date2, margin });
    },
    getFilter(
      context,
      {
        cli,
        pro,
        pay,
        ord,
        reg,
        cam,
        media,
        link,
        ord_s,
        ord_r,
        del_f,
        ga1,
        ga2,
        ga3
      }
    ) {
      //篩選改變
      context.commit("sendFilter", {
        cli,
        pro,
        pay,
        ord,
        reg,
        cam,
        media,
        link,
        ord_s,
        ord_r,
        del_f,
        ga1,
        ga2,
        ga3
      });
    },
    getDownload(context) {
      //獲取下載api
      let margin = context.state.margin;
      let date1 = context.state.date1;
      let date2 = context.state.date2;
      let cli = context.state.cli;
      let pro = context.state.pro;
      let pay = context.state.pay;
      let ord = context.state.ord;
      let reg = context.state.reg;
      let cam = context.state.cam;
      let media = context.state.media;
      let link = context.state.link;
      let ga1 = context.state.ga1;
      let ga2 = context.state.ga2;
      let ga3 = context.state.ga3;
      context.commit("sendDownApi", {
        margin,
        date1,
        date2,
        cli,
        pro,
        pay,
        ord,
        reg,
        cam,
        media,
        link,
        ga1,
        ga2,
        ga3
      });
    },
    getData(context) {
      // cros 的 api
      context.commit("sendLoading", true);
      let date1 = context.state.date1;
      let date2 = context.state.date2;
      let margin = context.state.margin;
      let cli = context.state.cli;
      let pro = context.state.pro;
      let pay = context.state.pay;
      let ord = context.state.ord;
      let reg = context.state.reg;
      let cam = context.state.cam;
      let media = context.state.media;
      let link = context.state.link;
      let ord_s = context.state.ord_s;
      let ord_r = context.state.ord_r;
      let del_f = context.state.del_f;
      let ga1 = context.state.ga1;
      let ga2 = context.state.ga2;
      let ga3 = context.state.ga3;

      let api = `${process.env.APIPATH}/${process.env.DORENTO}`;
      console.log(api);     
      $("*").css("cursor", `url('/static/images/wait.ico') ,auto`);
      axios.get(api).then(res => {
        if (res) {
          let data = res.data;
          console.log(data);
          context.commit("sendData", data);
          context.commit("sendLoading", false);
          $("*").css("cursor", `url('/static/images/move.ico') ,auto`);
        } else {
          return;
        }
      });
    } // cros end
  }, // ACT END
  mutations: {
    sendLoading(state, status) {
      state.isLoading = status;
    }, // 變更loading 資料
    sendFilter(
      state,
      {
        cli,
        pro,
        pay,
        ord,
        reg,
        cam,
        media,
        link,
        ord_s,
        ord_r,
        del_f,
        ga1,
        ga2,
        ga3
      }
    ) {
      state.cli = cli;
      state.pro = pro;
      state.pay = pay;
      state.ord = ord;
      state.reg = reg;
      state.cam = cam;
      state.media = media;
      state.link = link;
      state.ord_s = ord_s;
      state.ord_r = ord_r;
      state.del_f = del_f;
      state.ga1 = ga1;
      state.ga2 = ga2;
      state.ga3 = ga3;
      // 變更參數資料
    },
    sendDate(state, { date1, date2, margin }) {
      state.date1 = date1;
      state.date2 = date2;
      state.margin = margin;
      // 變更時間資料
    },
    sendDateType(state, type) {
      state.dateType = type;
    },
    sendData(state, data) {
      state.data = data;
      // 變更資料包
    },

    sendDownApi(
      state,
      {
        margin,
        date1,
        date2,
        cli,
        pro,
        pay,
        ord,
        reg,
        cam,
        media,
        link,
        ga1,
        ga2,
        ga3
      }
    ) {
      state.download_api = `${process.env.APIPATH}/${process.env.DORENTO_DOWNLOAD}`;
    } // 變更下載api
  }
};



